import React, {useState} from "react"
import "./Partner.css"
import banner from "../../src/media/partnerreg.jpg"
import ReCAPTCHA from "react-google-recaptcha";
import emailjs from "emailjs-com"

export default function Partner () {
    const [captcha,setCaptcha] = useState(false)
    function captchaDone () {
        const but = document.getElementById("send")
        const checkbox = document.getElementById("terms")
        setCaptcha(true)
        if(checkbox.checked) {
            but.className = "activeSend"
        }
    }
    const submit = (e) => {
        const checkbox = document.getElementById("terms")
        e.preventDefault();
        if(checkbox.checked && captcha) {
            const inputs = [...document.querySelectorAll("input")]
            let normal = true
            inputs.forEach((el) => {
                if(el.value === "") {
                    normal = false
                    el.className += " error"
                }
                else {
                    if(el.id !== "terms") {el.className = ""}
                }
                document.getElementById("send").className = "contactInput"
            })
            if(!normal) {
                document.getElementById("terms").checked = false
                return
            }
            emailjs.sendForm('service_85keurj', 'template_1z8a80n', e.target, 'user_I9RkM2wjvwskcN9ZDoUS0')
            .then((result) => {
                document.getElementById("send").value = "Sent!"
                e.target.reset()
                document.getElementById("terms").checked = false
            }, (error) => {
                console.log(error.text);
            });
        }
        
    };
    function checkbox (e) {
        const but = document.getElementById("send")
        if(e.target.checked && captcha) {but.className = "activeSend"}
        if(!e.target.checked) {but.className = ""}
    }
    return (
        <>
            <img className="banners" src={banner} alt="" />
            <section id="partnerSec">
                <h1>Absolut xFusion Partner registration</h1>
                <form onSubmit={submit} id="partnerForm" action="">
                    <div className="formColumn">
                    <select name="gender">
                        <option value="male">Male</option>
                        <option value="female">Female</option>
                    </select>
                        <input name="name" placeholder="Name" type="text" />
                        <input name="surname" placeholder="Surname" type="text" />
                    </div>
                    <div className="formColumn">
                        <input name="location" placeholder="Location" type="text" />
                        <input name="postal" placeholder="Postal Code" type="text" />
                        <input name="street" placeholder="Street" type="text" />
                    </div>
                    <div className="formColumn">
                        <input name="func" placeholder="Function" type="text" />
                        <input name="company" placeholder="Company" type="text" />
                        <input name="phone" placeholder="Mobile phone" type="tel" />
                    </div>
                    <div className="formColumn">
                        <input name="email" placeholder="Email" type="email" />
                        <input name="message" placeholder="Message" type="text" />
                        <input type="submit" id="send" value="Send" />
                    </div>
                </form>
                <div id="termsWrapper">
                    <div>
                        <input onChange={checkbox} type="checkbox" id="terms" name="scales"/>
                        <label for="terms">I agree that my details from the contact form will be collected and processed to answer my request in accordance with the data protection declaration.</label>
                    </div>
                </div>
                <ReCAPTCHA
                    sitekey="6LeIxAcTAAAAAJcZVRqyHh71UMIEGNQ_MXjiZKhI"
                    onChange={captchaDone}
                />,
            </section>
        </>
    )
}